import {Injectable} from '@angular/core';
import {AdditionalFilter, Floor, PlaceOptions, Repetitive, SeatType} from '@pages/booking-search/entities';
import {Observable, of} from 'rxjs';
import {BookingTableItem} from '@pages/booking-search/booking-search-table/booking-table-item';
import {ApiService} from '../../api.service';
import {Office} from '@pages/booking-search/entities';
import {take} from 'rxjs/operators';
import {map} from 'rxjs/internal/operators';
import {User} from 'app/entities/user';

@Injectable(
	/*{
	providedIn: BookingSearchModule
}*/
	{
		providedIn: 'root' // Explanation: I noticed a warning about circular dependency this was an easy fix
	}
)
export class BookingSearchFiltersService extends ApiService {
	getAvailableOffices(cityID: number): Observable<Office[]> {
		return this.get<Office[]>(`/offices/byCity/${cityID}`).pipe(take(1));
	}
	getAvailableFloors(officeID: number): Observable<Floor[]> {
		return this.get<Floor[]>(`/floors/byOffice/${officeID}`).pipe(take(1));
	}
	getAvailableRepetitive(userID: number): Observable<Repetitive[]> {
		// Is not implemented in the back
		// return this.get<SeatType[]>(`/floors/byOffice/${userID}`).pipe(take(1));
		return of([
			{
				id: 1,
				name: 'Single reservation'
			},
			{
				id: 2,
				name: ' Repetitive reservation'
			}
		]);
	}
	getAvailableSeatTypes(userID: number): Observable<SeatType[]> {
		// Is not implemented in the back
		// return this.get<SeatType[]>(`/floors/byOffice/${userID}`).pipe(take(1));
		return of([
			{
				id: 1,
				name: 'Place in coworking'
			},
			{
				id: 2,
				name: ' Conference room'
			}
		]);
	}
	getAvailableAdditionalFilters(/*officeID: number*/): Observable<AdditionalFilter[]> {
		/*${officeID}*/
		return this.get<AdditionalFilter[]>(`/tags`).pipe(take(1));
	}

	getBookingSearchTable(filters /*: string*/): Observable<BookingTableItem[]> {
		return this.post<BookingTableItem[]>(`/reservationInfo/free`, filters).pipe(
			map((data: any) => {
				return data.map((item: any) => {
					return {
						currentUserId: item.currentUserId,
						placeId: item.placeId,
						placeName: item.placeName,
						floorNumber: item.floorNumber,
						startTime: item.startTime,
						endTime: item.endTime,
						startDate: item.startDate,
						endDate: item.endDate,
						tags: item.tags,
						weekDay: item.weekDay,
						tagsS: item.tags.map((tag) => tag.name)
					};
				});
			})
		);
	}

	getPlaceOptions(filters /*: string*/): Observable<PlaceOptions[]> {
		return this.post<PlaceOptions[]>(`/reservationInfo/free`, filters).pipe(
			map((data: any) => {
				return data.map((item: any) => {
					return {
						currentUserId: item.currentUserId,
						placeId: item.placeId,
						placeName: item.placeName,
						floorNumber: item.floorNumber,
						startTime: item.startTime,
						endTime: item.endTime,
						startDate: item.startDate,
						endDate: item.endDate,
						tags: item.tags,
						weekDay: item.weekDay,
						tagsS: item.tags.map((tag) => tag.name)
					};
				});
			})
		);
	}

	getBookingPlacesIds(filters: string): Observable<BookingTableItem[]> {
		return this.post<BookingTableItem[]>(`/reservationInfo/free`, filters).pipe(take(1));
	}

	getCurrentUser(): Observable<User> {
		return this.get<User>(`/users/current`).pipe(take(1));
	}

	getBookingUser(id: number): Observable<User> {
		return this.get<User>(`/users/${id}`).pipe(take(1));
	}

	postReservation(reservation: string): Observable<BookingTableItem> {
		return this.post<BookingTableItem>(`/reservationInfo/save`, reservation).pipe(take(1));
	}
	validateReservation(reservation: string): Observable<BookingTableItem[]> {
		return this.post<BookingTableItem[]>(`/reservationInfo/showIntersection`, reservation).pipe(
			map((data: any) => {
				return data.map((item: any) => {
					return {
						currentUserId: item.currentUserId,
						placeId: item.placeId,
						placeName: item.placeName,
						floorNumber: item.floorNumber,
						startTime: item.startTime,
						endTime: item.endTime,
						startDate: item.startDate,
						endDate: item.endDate
					};
				});
			})
		);
	}
}
