import {Injectable} from '@angular/core';
import {ApiService} from '../../api.service';
import {Observable} from 'rxjs';
import {GetUsersParams, Role, SetRolesParams, User} from '@pages/user-management/entities';
import {map} from 'rxjs/internal/operators';
import {HttpParams} from '@angular/common/http';

@Injectable()
export class UsersService extends ApiService {
	getUsers(
		params: GetUsersParams = {pageNumber: 0, pageSize: 10, sortMethod: 'id', sortDirection: 'ASC', username: ''}
	): Observable<User[]> {
		let parameters = new HttpParams();
		parameters = parameters.append('pageNumber', `${params.pageNumber}`);
		parameters = parameters.append('pageSize', `${params.pageSize}`);
		parameters = parameters.append('sortMethod', params.sortMethod);
		parameters = parameters.append('sortDirection', params.sortDirection);
		parameters = parameters.append('username', params.username);
		return this.get<User[]>(`/users/extended/paging`, {params: parameters}).pipe(
			map((data: any) => {
				return data.map((item: any) => {
					return {
						id: item.id,
						name: item.name,
						email: item.email,
						roles: item.roles,
						hr: item.hr?.name || null
					};
				});
			})
		);
	}

	getUsersByNameCount(searchValue: string = ''): Observable<number> {
		let parameters = new HttpParams();
		parameters = parameters.append('username', `${searchValue}`);
		return this.get<number>(`/users/extended/paging/count`, {params: parameters});
	}
	getRoles(): Observable<Role[]> {
		return this.get<Role[]>(`/roles`);
	}
	setRoles(params: SetRolesParams): Observable<Role[]> {
		return this.post<Role[]>(`/users/roles/${params.userId}`, params.roles);
	}
}
