import {Component, HostListener, Inject} from '@angular/core';
import {MAT_DIALOG_DATA, MatDialogRef} from '@angular/material/dialog';
import {MapBuilderService} from '@shared/map-builder/map-builder.service';
import {FormControl} from '@angular/forms';

@Component({
	selector: 'load-image-dialog',
	templateUrl: './load-image-dialog.component.html',
	styleUrls: ['./load-image-dialog.component.scss']
})
export class LoadImageDialogComponent {
	uploadController = new FormControl();
	private file: File | null = null;

	@HostListener('change', ['$event.target.files']) emitFiles(event: FileList) {
		this.file = event && event.item(0);
	}
	constructor(
		public dialogRef: MatDialogRef<LoadImageDialogComponent>,
		private servicePlanUpload: MapBuilderService,
		@Inject(MAT_DIALOG_DATA) public data
	) {}

	onUpload() {
		this.data.link = `http://hotchairs.herokuapp.com/api/floors/plan/${this.data.floorId}`;
		if (this.uploadController.value !== null) {
			this.servicePlanUpload.postPlan(this.data.floorId, this.file);
			this.data.link = `http://hotchairs.herokuapp.com/api/floors/plan/${this.data.floorId}`;
			this.dialogRef.close(this.data.link);
		}
	}
}
