import {
	AfterViewInit,
	Component,
	OnInit,
	ViewChild,
	Input,
	OnChanges,
	SimpleChanges,
	Output,
	EventEmitter,
	OnDestroy
} from '@angular/core';
import {MatDialog} from '@angular/material/dialog';
import {MatPaginator} from '@angular/material/paginator';
import {MatSort} from '@angular/material/sort';
import {FloorsDialogComponent} from '@pages/offices/floors-dialog/floors-dialog.component';
import {OfficesTableItem} from '../entities';
import {merge} from 'rxjs';
import {startWith, map, delay} from 'rxjs/internal/operators';
import {DeleteParams, GetOfficesParams} from '@pages/offices/entities';
import {DeleteOfficeDialogComponent} from '@pages/offices/delete-office-dialog/delete-office-dialog.component';

@Component({
	selector: 'app-offices-table',
	templateUrl: './offices-table.component.html',
	styleUrls: ['./offices-table.component.scss']
})
export class OfficesTableComponent implements AfterViewInit, OnInit, OnChanges, OnDestroy {
	@ViewChild(MatPaginator) paginator: MatPaginator;
	@ViewChild(MatSort) sort: MatSort;
	@Input() offices: OfficesTableItem[];
	@Output() getOffices = new EventEmitter<GetOfficesParams>();
	@Input() officesCount: number;
	@Output() getOfficesCount = new EventEmitter<void>();
	@Output() updateTable = new EventEmitter<DeleteParams>();
	loading = false;
	data = [];
	subscription;

	displayedColumns = ['country', 'city', 'address', 'edit', 'delete'];

	constructor(public dialog: MatDialog) {}

	ngOnInit() {}

	ngOnChanges(changes: SimpleChanges) {
		if (changes.offices && this.offices) {
			this.data = this.offices;
		}
	}

	ngAfterViewInit() {
		this.sort.sortChange.subscribe(() => (this.paginator.pageIndex = 0));

		merge(this.sort.sortChange, this.paginator.page)
			.pipe(
				startWith({}),
				delay(0),
				map(() => {
					this.loading = true;
					this.getOffices.emit({
						pageNumber: this.paginator.pageIndex,
						pageSize: this.paginator.pageSize,
						sortMethod: this.sort.direction ? this.sort.active : 'id',
						sortDirection: this.sort.direction ? this.sort.direction.toUpperCase() : 'DESC'
					});
					this.loading = false;
					this.getOfficesCount.emit();
					return this.offices;
				})
			)
			.subscribe((data) => {
				this.data = data;
			});
	}

	openFloorsDialog(office) {
		this.dialog.open(FloorsDialogComponent, {
			data: office
		});
	}

	openDeleteDialog(office) {
		this.subscription = this.dialog
			.open(DeleteOfficeDialogComponent, {
				data: office
			})
			.afterClosed()
			.subscribe((res) => {
				if (res) {
					this.updateTable.emit({
						officeId: office.officeId,
						params: {
							pageNumber: this.paginator.pageIndex,
							pageSize: this.paginator.pageSize,
							sortMethod: this.sort.direction ? this.sort.active : 'id',
							sortDirection: this.sort.direction ? this.sort.direction.toUpperCase() : 'DESC'
						}
					});
				}
			});
	}

	ngOnDestroy() {
		if (this.subscription) {
			this.subscription.unsubscribe();
			this.subscription = null;
		}
	}
}
