import {Injectable} from '@angular/core';
import {CountryCardModel} from './country-card-model';
import {Observable} from 'rxjs';
import {ApiService} from '../../../api.service';

@Injectable()
export class CardService extends ApiService {
	getCountries(): Observable<CountryCardModel[]> {
		return this.get<CountryCardModel[]>('/countries/extended');
	}
}
