import {Component, OnInit} from '@angular/core';
import {UserBookingsService} from '@pages/user-bookings/user-bookings.service';
import {Observable, Subscription} from 'rxjs';
import {Reservation} from '@pages/user-bookings/entities';

@Component({
	selector: 'user-bookings',
	templateUrl: './user-bookings.component.html',
	styleUrls: ['./user-bookings.component.scss']
})
export class UserBookingsComponent implements OnInit {
	bookings$;
	subscription: Subscription = new Subscription();

	constructor(private userBookingsService: UserBookingsService) {}

	ngOnInit(): void {
		this.bookings$ = this.getBookings();
	}

	getBookings(): Observable<Reservation[]> {
		return this.userBookingsService.getBookings();
	}

	updateTable(id: number) {
		this.subscription.add(
			this.deleteBooking(id).subscribe(() => {
				this.bookings$ = this.getBookings();
			})
		);
	}

	deleteBooking(id: number): Observable<Reservation> {
		return this.userBookingsService.deleteBooking(id);
	}
}
