import {
	AfterViewInit,
	Component,
	EventEmitter,
	Input,
	OnChanges,
	OnDestroy,
	OnInit,
	Output,
	SimpleChanges,
	ViewChild
} from '@angular/core';
import {MatPaginator} from '@angular/material/paginator';
import {MatSort} from '@angular/material/sort';
import {DatePipe} from '@angular/common';
import {Reservation} from '@pages/user-bookings/entities';
import {MatDialog} from '@angular/material/dialog';
import {CommentDialogComponent} from '@pages/user-bookings/comment-dialog/comment-dialog.component';
import {Subscription} from 'rxjs';
import {DeleteDialogComponent} from '@pages/user-bookings/delete-dialog/delete-dialog.component';

@Component({
	selector: 'bookings-table',
	templateUrl: './bookings-table.component.html',
	styleUrls: ['./bookings-table.component.scss']
})
export class BookingsTableComponent implements OnInit, AfterViewInit, OnChanges, OnDestroy {
	displayedColumns: string[] = ['place', 'time', 'date', 'floor', 'location', 'actions'];
	@ViewChild(MatPaginator) paginator: MatPaginator;
	@ViewChild(MatSort) sort: MatSort;
	@Input() bookings: Reservation[];
	@Output() updateTable = new EventEmitter<number>();
	dataSource: Reservation[] = [];
	loading = false;
	currentDate: string;
	subscription: Subscription = new Subscription();

	constructor(private datePipe: DatePipe, public dialog: MatDialog) {
		this.currentDate = this.datePipe.transform(new Date(), 'yyyy-MM-dd');
	}

	ngOnInit(): void {}

	ngOnChanges(changes: SimpleChanges) {
		if (changes.bookings && this.bookings) {
			this.dataSource = this.bookings;
		}
	}

	ngAfterViewInit() {}

	showCommentDialog(placeId: number, userId: number) {
		this.subscription.add(
			this.dialog
				.open(CommentDialogComponent, {
					data: {
						placeId,
						userId
					}
				})
				.afterClosed()
				.subscribe((comment) => console.log(comment))
		);
	}

	showDeleteDialog(element: Reservation) {
		this.subscription.add(
			this.dialog
				.open(DeleteDialogComponent, {
					data: element
				})
				.afterClosed()
				.subscribe((result) => {
					if (result) {
						this.updateTable.emit(element.id);
					}
				})
		);
	}

	ngOnDestroy() {
		this.subscription.unsubscribe();
	}
}
