import {Component, Inject, OnDestroy, OnInit} from '@angular/core';
import {MAT_DIALOG_DATA, MatDialog, MatDialogRef} from '@angular/material/dialog';
import {FormBuilder, FormGroup, Validators} from '@angular/forms';
import {FloorsService} from '@pages/offices/floors-dialog/floors.service';
import {map} from 'rxjs/internal/operators';
import {Router} from '@angular/router';
import {WarningDialogComponent} from '@pages/offices/warning-dialog/warning-dialog.component';
import {Subscription} from 'rxjs';

@Component({
	selector: 'floors-dialog',
	templateUrl: './floors-dialog.component.html',
	styleUrls: ['./floors-dialog.component.scss']
})
export class FloorsDialogComponent implements OnInit, OnDestroy {
	formGroup: FormGroup;
	showHint = false;
	subscription: Subscription = new Subscription();

	constructor(
		private router: Router,
		@Inject(MAT_DIALOG_DATA) public data: any,
		private _formBuilder: FormBuilder,
		private floorsService: FloorsService,
		private dialogRef: MatDialogRef<FloorsDialogComponent>,
		public dialog: MatDialog
	) {}

	ngOnInit(): void {
		this.formGroup = this._formBuilder.group({
			floorControl: ['', [Validators.required, Validators.maxLength(3), Validators.pattern('[0-9]+')]]
		});
		this.data.floors.sort(function(a, b) {
			if (a.number > b.number) {
				return 1;
			}
			if (a.number < b.number) {
				return -1;
			}
			return 0;
		});
	}

	ngOnDestroy() {
		this.subscription.unsubscribe();
	}

	navigateToEdit(floorId: number): void {
		this.close();
		this.router.navigateByUrl(`/canvas/${floorId}`);
	}

	deleteFloor(floorId: number): void {
		this.subscription.add(
			this.floorsService.deleteFloor(floorId).subscribe(
				() => {
					this.data.floors = this.data.floors.filter((floor) => floor.id !== floorId);
				},
				(error) => {
					if (error.status === 409) {
						this.dialog.open(WarningDialogComponent, {
							data: {
								title: "Sorry, we can't delete this floor",
								text: 'Some of its places are booked by employees.'
							}
						});
					}
				}
			)
		);
	}

	addFloor() {
		const newFloor: number = +this.formGroup.get('floorControl').value;
		const floorNumbers = this.data.floors.map((floor) => floor.number);
		console.log(floorNumbers);
		if (floorNumbers.includes(newFloor)) {
			this.showHint = true;
			return;
		}
		this.showHint = false;
		this.subscription.add(
			this.floorsService
				.addFloor({number: newFloor, officeId: this.data.officeId})
				.pipe(
					map((data: any) => {
						return data;
					})
				)
				.subscribe((res) => {
					this.data.floors.push(res);
				})
		);
	}

	close() {
		this.dialogRef.close();
	}
}
