import {Injectable} from '@angular/core';
import {ApiService} from '../../api.service';
import {TagsModel} from '@shared/map-builder/tags-model';
import {Observable} from 'rxjs';
import {PlaceMoreModel} from '@shared/map-builder/place-more-model';

@Injectable()
export class MapBuilderService extends ApiService {
	getTags(): Observable<TagsModel[]> {
		return this.get<TagsModel[]>('/tags');
	}
	resetPlacesId(floorId: number, placeInfo: PlaceMoreModel[]): Observable<PlaceMoreModel[]> {
		return this.post<PlaceMoreModel[]>(`/places/batch/${floorId}`, placeInfo);
	}
	postMap(floorId: number, svgMap: string): void {
		this.post<void>(`/floors/map/${floorId}`, svgMap).subscribe();
	}

	postPlan(floorId: number, file: File): void {
		this.post<void>(`/floors/plan/${floorId}`, file).subscribe();
	}

	getMap(floorId: number, params): Observable<string> {
		return this.get<string>(`/floors/map/${floorId}`, params);
	}
	getExistedPlaces(floorId: number): Observable<PlaceMoreModel[]> {
		return this.get<PlaceMoreModel[]>(`/places/byFloor/${floorId}`);
	}
}
