import {Component, Inject, OnDestroy, OnInit} from '@angular/core';
import {Observable} from 'rxjs';
import {OfficeCreationService} from './office-creation.service';
import {Country, City, Office} from '@pages/office-creation/entities';
import {map, take} from 'rxjs/internal/operators';
import {MAT_DIALOG_DATA, MatDialogRef} from '@angular/material/dialog';
import {FloorsDialogComponent} from '@pages/offices/floors-dialog/floors-dialog.component';

@Component({
	selector: 'office-creation',
	templateUrl: './office-creation.component.html',
	styleUrls: ['./office-creation.component.scss']
})
export class OfficeCreationComponent implements OnInit, OnDestroy {
	countries$;
	cities$;
	subscription;

	constructor(
		private officeCreationService: OfficeCreationService,
		@Inject(MAT_DIALOG_DATA) public data: any,
		private dialogRef: MatDialogRef<FloorsDialogComponent>
	) {}

	ngOnInit() {
		this.countries$ = this.getCountries().pipe(take(1));
	}

	ngOnDestroy() {
		if (this.subscription) {
			this.subscription.unsubscribe();
			this.subscription = null;
		}
	}

	getCountries(): Observable<Country[]> {
		return (this.countries$ = this.officeCreationService.getCountries());
	}

	setCities(id: number): void {
		this.cities$ = this.getCities(id).pipe(take(1));
	}

	getCities(id: number): Observable<City[]> {
		return (this.cities$ = this.officeCreationService.getCities(id));
	}

	addOffice(office: Office): void {
		this.subscription = this.officeCreationService
			.addOffice(office)
			.pipe(
				map((data: any) => {
					return data;
				})
			)
			.subscribe((res) => {
				this.dialogRef.close(res);
			});
	}

	close() {
		this.dialogRef.close();
	}
}
