import {
	ChangeDetectionStrategy,
	ChangeDetectorRef,
	Component,
	EventEmitter,
	Input,
	OnChanges,
	OnInit,
	Output,
	SimpleChanges
} from '@angular/core';
import {fabric} from 'fabric';
import {Observable} from 'rxjs';
import {HttpHeaders} from '@angular/common/http';
import {MapForBookingService} from '@shared/map-for-booking/map-for-booking.service';
import {PlaceMoreModel} from '@shared/map-builder/place-more-model';

@Component({
	selector: 'map-for-booking',
	templateUrl: './map-for-booking.component.html',
	styleUrls: ['./map-for-booking.component.scss'],
	changeDetection: ChangeDetectionStrategy.OnPush
})
export class MapForBookingComponent implements OnInit, OnChanges {
	@Input() places;
	@Output() chosenPlaceId = new EventEmitter();
	canvas: any;
	floorId: number;
	placeInfo$: Observable<PlaceMoreModel>;
	tags: string[] = [];
	capacity: number;
	nameOfPlace: string;
	SVGSHE4KANEW$: Observable<string>;

	constructor(private serviceForOrders: MapForBookingService, private cd: ChangeDetectorRef) {}

	ngOnInit(): void {}

	loadFromSVG() {
		const cnv = this.canvas;
		cnv.clear();
		cnv.setZoom(0.6);
		this.getConsultationPointSOAPWs();
		this.SVGSHE4KANEW$.subscribe((elem) => {
			fabric.loadSVGFromString(elem, (objects) => {
				const offsetLeft = objects[0].left;
				const offsetTop = objects[0].top;
				objects.forEach((obj) => {
					obj.left = obj.left - offsetLeft + document.getElementById('canvasBlock').offsetWidth / 2;
					obj.top = obj.top - offsetTop + document.getElementById('canvasBlock').offsetHeight / 8;
					obj.lockMovementX = true;
					obj.lockMovementY = true;
					obj.setControlsVisibility({
						mt: false,
						mb: false,
						mr: false,
						ml: false,
						bl: false,
						tl: false,
						br: false,
						tr: false,
						mtr: false
					});
					cnv.add(obj).setActiveObject(obj);
					this.grayScaleCheck(cnv.getActiveObject());
					cnv.discardActiveObject();
					cnv.renderAll();
				});
			});
		});
	}

	getConsultationPointSOAPWs() {
		return (this.SVGSHE4KANEW$ = this.serviceForOrders.getMap(this.floorId, {
			headers: new HttpHeaders({
				'Content-Type': 'application/xml'
			}),
			responseType: 'text'
		}));
	}

	grayScaleCheck(activeObject) {
		let grayscaleTrigger = true;
		this.places.places.forEach((place) => {
			if (place.placeId === activeObject.id) {
				grayscaleTrigger = false;
			}
		});
		if (
			grayscaleTrigger &&
			activeObject.id !== 'map' &&
			activeObject.id !== 'wall' &&
			activeObject.id !== 'window' &&
			activeObject.id !== 'pot'
		) {
			activeObject.filters.push(new fabric.Image.filters.Grayscale());
			activeObject.applyFilters();
			activeObject.selectable = false;
		} else {
			if (
				activeObject.id === 'map' ||
				activeObject.id === 'wall' ||
				activeObject.id === 'pot' ||
				activeObject.id === 'window'
			) {
				activeObject.selectable = false;
			}
			grayscaleTrigger = true;
		}
	}

	getPlaceInfo(placeId: number) {
		return (this.placeInfo$ = this.serviceForOrders.getPlaceInfo(placeId));
	}

	ngOnChanges(changes: SimpleChanges): void {
		if (changes.places) {
			this.floorId = this.places.floorId;
			this.canvas = new fabric.Canvas('canvas', {allowTouchScrolling: true});
			this.canvas.on('mouse:wheel', (opt) => {
				const delta = opt.e.deltaY;
				let zoom = this.canvas.getZoom();
				zoom *= 0.999 ** delta;
				if (zoom > 1) {
					zoom = 1;
				}
				if (zoom < 0.6) {
					zoom = 0.6;
				}
				console.log(zoom);
				this.canvas.zoomToPoint({x: opt.e.offsetX, y: opt.e.offsetY}, zoom);
				opt.e.preventDefault();
				opt.e.stopPropagation();
			});
			this.canvas.on('mouse:dblclick', () => {
				if (this.canvas.getActiveObject()) {
					this.chosenPlaceId.emit(this.canvas.getActiveObject().id);
				}
			});
			this.canvas.on('mouse:over', (e) => {
				if (e.target !== null && e.target.selectable === true) {
					this.tags = [];
					this.capacity = 0;
					document.getElementById('selectId').style.display = 'block';
					this.places.places
						.find((place) => place.placeId === e.target.id)
						.tags.forEach((tag) => {
							this.tags.push(tag);
						});
					this.getPlaceInfo(e.target.id).subscribe((elem) => {
						this.capacity = elem.capacity;
						this.nameOfPlace = elem.name;
						this.cd.detectChanges();
					});
				}
			});

			this.canvas.on('mouse:out', (e) => {
				if (e.target !== null && e.target.selectable === true) {
					document.getElementById('selectId').style.display = 'none';
				}
			});
			this.loadFromSVG();
		}
	}

	confirmAdditions() {
		this.tags = [];
		document.getElementById('selectId').style.display = 'none';
	}
}
