import {
	ChangeDetectionStrategy,
	ChangeDetectorRef,
	Component,
	EventEmitter,
	Input,
	OnChanges,
	OnDestroy,
	OnInit,
	Output
} from '@angular/core';
import {default as _rollupMoment} from 'moment';
import * as _moment from 'moment';
import {FormControl, FormGroup} from '@angular/forms';
import {StaffBookingsService} from '@pages/staff-bookings/staff-bookings.service';
import {DateAdapter, MAT_DATE_FORMATS, MAT_DATE_LOCALE} from '@angular/material/core';
import {MAT_MOMENT_DATE_FORMATS, MomentDateAdapter} from '@angular/material-moment-adapter';

const current = _rollupMoment || _moment;

@Component({
	selector: 'filterbar',
	templateUrl: './filterbar.component.html',
	styleUrls: ['./filterbar.component.scss'],
	providers: [
		{provide: DateAdapter, useClass: MomentDateAdapter, deps: [MAT_DATE_LOCALE]},
		{provide: MAT_DATE_FORMATS, useValue: MAT_MOMENT_DATE_FORMATS}
	],
	changeDetection: ChangeDetectionStrategy.OnPush
})
export class FilterbarComponent implements OnInit, OnChanges, OnDestroy {
	countryId: number;
	cityId: number;
	@Input() countriesToChoose;
	@Input() employeesToChoose;
	@Input() citiesToChoose;
	@Output() setCountry = new EventEmitter<any>();
	@Input() officesToChoose;
	@Output() setCity = new EventEmitter<any>();
	@Output() filtersChanged = new EventEmitter();
	subscription;

	today = current().format('YYYY-MM-DD');

	staffFilters = new FormGroup({
		employee: new FormControl(''),
		country: new FormControl(''),
		city: new FormControl(''),
		office: new FormControl(''),
		date: new FormControl('')
	});

	constructor(private staffBookingsService: StaffBookingsService, private cd: ChangeDetectorRef) {}

	ngOnInit(): void {
		current.locale('en-uk');
		if (!this.countriesToChoose) {
			this.countriesToChoose = [];
		}
		if (!this.citiesToChoose) {
			this.citiesToChoose = [];
		}
		if (!this.officesToChoose) {
			this.officesToChoose = [];
		}
	}

	ngOnChanges() {}

	ngOnDestroy() {
		if (this.subscription) {
			this.subscription.unsubscribe();
			this.subscription = null;
		}
	}

	countryChange() {
		if (this.staffFilters.get('country').value !== this.countryId) {
			this.staffFilters.reset({
				employee: this.staffFilters.get('employee').value,
				country: this.staffFilters.get('country').value,
				city: '',
				office: '',
				date: this.staffFilters.get('date').value
			});
			if (this.staffFilters.get('country').value !== undefined) {
				this.countryId = this.staffFilters.get('country').value;
				this.setCountry.emit(this.countryId);
				this.setCity.emit(this.countryId);
			}
		}
		this.onSearch();
	}

	cityChange() {
		if (this.staffFilters.get('city').value !== this.cityId) {
			this.staffFilters.reset({
				employee: this.staffFilters.get('employee').value,
				country: this.staffFilters.get('country').value,
				city: this.staffFilters.get('city').value,
				office: '',
				date: this.staffFilters.get('date').value
			});
			if (this.staffFilters.get('city').value !== undefined) {
				this.cityId = this.staffFilters.get('city').value;
				this.setCity.emit(this.cityId);
			}
		}
		this.onSearch();
	}

	onSearch(): void {
		const data: string[] = [];

		if (this.staffFilters.get('employee').value) {
			data.push(this.staffFilters.get('employee').value);
		} else data.push('');

		if (this.staffFilters.get('country').value && this.staffFilters.get('country').value !== '') {
			data.push(this.countriesToChoose.find((item) => item.id === this.staffFilters.get('country').value).name);
		} else {
			data.push('');
		}

		if (this.staffFilters.get('city').value && this.staffFilters.get('city').value !== '') {
			data.push(this.citiesToChoose.find((item) => item.id === this.staffFilters.get('city').value).name);
		} else {
			data.push('');
		}

		if (this.staffFilters.get('office').value) {
			data.push(this.staffFilters.get('office').value);
		} else data.push('');

		if (this.staffFilters.get('date').value && this.staffFilters.get('date').value !== '') {
			this.staffFilters.patchValue({
				date: current(this.staffFilters.getRawValue().date).format('YYYY-MM-DD')
			});
			data.push(this.staffFilters.get('date').value);
		} else {
			data.push('');
		}

		this.filtersChanged.emit(data);
	}
}
