import {Component, Inject, OnInit} from '@angular/core';
import {MAT_DIALOG_DATA, MatDialogRef} from '@angular/material/dialog';
import {FormControl, FormGroup, Validators} from '@angular/forms';

@Component({
	selector: 'comment-dialog',
	templateUrl: './comment-dialog.component.html',
	styleUrls: ['./comment-dialog.component.scss']
})
export class CommentDialogComponent implements OnInit {
	formGroup: FormGroup;

	constructor(@Inject(MAT_DIALOG_DATA) public data: any, private dialogRef: MatDialogRef<CommentDialogComponent>) {}

	ngOnInit(): void {
		this.formGroup = new FormGroup({
			commentControl: new FormControl('', [Validators.required, Validators.pattern('[\\s\\S]*\\S[\\s\\S]*')])
		});
	}

	close() {
		this.dialogRef.close();
	}

	submit() {
		this.dialogRef.close(this.formGroup.get('commentControl').value.trim());
	}
}
