import {ChangeDetectionStrategy, ChangeDetectorRef, Component, Input, OnInit} from '@angular/core';
import {take} from 'rxjs/internal/operators';
import {Observable, Subscription} from 'rxjs';
import {City, Country, Employee, Office} from '@pages/staff-bookings/entities';
import {StaffBookingsService} from '@pages/staff-bookings/staff-bookings.service';
import {AddingStaffBookingsComponent} from '@pages/adding-staff-bookings/adding-staff-bookings.component';
import {MatDialog} from '@angular/material/dialog';
import {StaffBTableItem} from '@pages/staff-bookings/staff-b-table/staff-b-table-item';

@Component({
	selector: 'staff-bookings',
	templateUrl: './staff-bookings.component.html',
	styleUrls: ['./staff-bookings.component.scss'],
	changeDetection: ChangeDetectionStrategy.OnPush
})
export class StaffBookingsComponent implements OnInit {
	countries$;
	cities$;
	offices$;
	employees$;
	staffTableData$;
	filters: string[] = ['', '', '', '', ''];
	subscription: Subscription = new Subscription();

	constructor(
		private staffBookingsService: StaffBookingsService,
		public dialog: MatDialog,
		private cd: ChangeDetectorRef
	) {}

	ngOnInit() {
		this.countries$ = this.getCountries().pipe(take(1));
		this.employees$ = this.getEmployees().pipe(take(1));
	}

	getCountries(): Observable<Country[]> {
		return (this.countries$ = this.staffBookingsService.getCountries());
	}

	getEmployees(): Observable<Employee[]> {
		return (this.employees$ = this.staffBookingsService.getEmployees());
	}

	setCountry(id: number): void {
		if (id) this.cities$ = this.getCities(id).pipe(take(1));
	}

	getCities(id: number): Observable<City[]> {
		return (this.cities$ = this.staffBookingsService.getCities(id));
	}

	setCity(id: number): void {
		if (id) this.offices$ = this.getOffices(id).pipe(take(1));
	}

	getOffices(id: number): Observable<Office[]> {
		return (this.offices$ = this.staffBookingsService.getOffices(id));
	}

	openAddingBookingDlg() {
		this.dialog.open(AddingStaffBookingsComponent);
	}

	getStaffTableData(filters = this.filters): Observable<StaffBTableItem[]> {
		this.cd.detectChanges();
		return (this.staffTableData$ = this.staffBookingsService.getStaffBookingsTable(filters));
	}

	updateTable(id: number) {
		this.subscription.add(
			this.deleteReservation(id).subscribe(() => {
				this.staffTableData$ = this.getStaffTableData(this.filters);
				this.cd.detectChanges();
			})
		);
	}

	deleteReservation(id: number): Observable<any> {
		return this.staffBookingsService.deleteReservation(id);
	}
}
