import {ChangeDetectionStrategy, Component, OnInit} from '@angular/core';
import {AddingStaffBookingsService} from '@pages/adding-staff-bookings/adding-staff-bookings.service';
import {take} from 'rxjs/internal/operators';
import {Observable} from 'rxjs';
import {City, Country, Employee} from '@pages/staff-bookings/entities';
import {MatDialogRef} from '@angular/material/dialog';
import {FloorsDialogComponent} from '@pages/offices/floors-dialog/floors-dialog.component';

@Component({
	selector: 'adding-staff-bookings',
	templateUrl: './adding-staff-bookings.component.html',
	styleUrls: ['./adding-staff-bookings.component.scss'],
	changeDetection: ChangeDetectionStrategy.OnPush
})
export class AddingStaffBookingsComponent implements OnInit {
	employees$;
	countries$;
	cities$;

	constructor(
		private addingStaffBookingsService: AddingStaffBookingsService,
		private dialogRef: MatDialogRef<FloorsDialogComponent>
	) {}

	ngOnInit(): void {
		this.countries$ = this.getCountries().pipe(take(1));
		this.employees$ = this.getEmployees().pipe(take(1));
	}

	getEmployees(): Observable<Employee[]> {
		return (this.employees$ = this.addingStaffBookingsService.getEmployees());
	}

	getCountries(): Observable<Country[]> {
		return (this.countries$ = this.addingStaffBookingsService.getCountries());
	}

	setCountry(id: number): void {
		this.cities$ = this.getCities(id).pipe(take(1));
	}

	getCities(id: number): Observable<City[]> {
		return (this.cities$ = this.addingStaffBookingsService.getCities(id));
	}

	close() {
		this.dialogRef.close();
	}
}
