import {Injectable} from '@angular/core';
import {ApiService} from '../../api.service';
import {Observable} from 'rxjs';
import {map} from 'rxjs/internal/operators';
import {Reservation} from '@pages/user-bookings/entities';

@Injectable()
export class UserBookingsService extends ApiService {
	getBookings(): Observable<Reservation[]> {
		return this.get<Reservation[]>('/users/current/reservations').pipe(
			map((data: any) => {
				return data.map((item: any) => {
					return {
						id: item.reservation.id,
						place: item.place.name,
						floor: item.floor.number,
						location: {
							city: item.city.name,
							country: item.country.name,
							address: item.office.street
						},
						date: {
							start: item.reservation.startDate,
							end: item.reservation.endDate,
							weekDays: item.reservation.weekDays
						},
						time: {
							start: item.reservation.startTime,
							end: item.reservation.endTime
						},
						placeId: item.place.id,
						userId: item.user.id
					};
				});
			})
		);
	}

	deleteBooking(id: number): Observable<Reservation> {
		return this.delete(`/reservations/${id}`);
	}
}
