import {ChangeDetectionStrategy, Component, OnInit, ViewChild} from '@angular/core';
import {MatSidenav} from '@angular/material/sidenav';
import {OrderService} from '@pages/main-page/side-nav-home-page/entity-orders/order.service';
import {Observable} from 'rxjs';
import {CountryCardModel} from '@shared/country-card/entity/country-card-model';
import {OrderModel} from '@pages/main-page/side-nav-home-page/entity-orders/order-model';
import {take} from 'rxjs/internal/operators';
import {CardService} from '@shared/country-card/entity/card.service';
import {BreakpointObserver, BreakpointState} from '@angular/cdk/layout';
import {CancelConfirmationComponent} from '@pages/main-page/side-nav-home-page/cancel-confirmation/cancel-confirmation.component';
import {MatDialog} from '@angular/material/dialog';
import {Router} from '@angular/router';

@Component({
	selector: 'side-nav-home-page',
	templateUrl: './side-nav-home-page.component.html',
	styleUrls: ['./side-nav-home-page.component.scss'],
	changeDetection: ChangeDetectionStrategy.OnPush
})
export class SideNavHomePageComponent implements OnInit {
	orders$: Observable<OrderModel[]>;
	nearestOrder$: Observable<OrderModel>;
	countries$: Observable<CountryCardModel[]>;
	ordersExist = true;
	columns: number;

	constructor(
		private serviceForOrders: OrderService,
		private serviceForCountries: CardService,
		public breakpointObserver: BreakpointObserver,
		private dialog: MatDialog,
		private router: Router
	) {}

	ngOnInit() {
		this.getOrders().pipe(take(1));
		this.getNearestOrder().pipe(take(1));
		this.getCountries().pipe(take(1));
		this.columnsIs();
		this.orders$.subscribe((elem) => {
			if (elem.length === 0) {
				this.ordersExist = false;
			}
		});
	}

	getOrders(): Observable<OrderModel[]> {
		return (this.orders$ = this.serviceForOrders.getOrders());
	}

	getNearestOrder(): Observable<OrderModel> {
		return (this.nearestOrder$ = this.serviceForOrders.getNearestOrders());
	}

	getCountries(): Observable<CountryCardModel[]> {
		return (this.countries$ = this.serviceForCountries.getCountries());
	}

	@ViewChild('sidenav') sidenav: MatSidenav;

	close() {
		this.sidenav.close();
	}

	onResize() {
		this.columnsIs();
	}

	columnsIs() {
		this.breakpointObserver.observe(['(max-width: 650px)']).subscribe((state: BreakpointState) => {
			if (state.matches) {
				this.columns = 1;
			} else {
				this.breakpointObserver.observe(['(max-width: 1000px)']).subscribe((state2: BreakpointState) => {
					if (state2.matches) {
						this.columns = 2;
					} else {
						this.breakpointObserver
							.observe(['(max-width: 1400px)'])
							.subscribe((state3: BreakpointState) => {
								if (state3.matches) {
									this.columns = 3;
								} else {
									this.columns = 4;
								}
							});
					}
				});
			}
		});
	}

	confirmCancellation(order: OrderModel) {
		const dialog = this.dialog.open(CancelConfirmationComponent, {data: {order: order}});
		dialog.afterClosed().subscribe((result) => {
			if (result) {
				this.cancelReservation(order.detail.reservationId);
				this.router.navigate(['home']);
			}
		});
	}

	cancelReservation(reservationId: number): void {
		this.serviceForOrders.deleteReservation(reservationId).subscribe();
		this.getOrders();
		this.getNearestOrder();
	}
}
