import {Injectable} from '@angular/core';
import {ApiService} from '../../api.service';
import {Observable} from 'rxjs';
import {OfficesTableItem} from '@pages/offices/entities';
import {map} from 'rxjs/internal/operators';
import {HttpParams} from '@angular/common/http';
import {GetOfficesParams} from './entities';

@Injectable()
export class OfficesService extends ApiService {
	getOffices(
		params: GetOfficesParams = {pageNumber: 0, pageSize: 10, sortMethod: 'id', sortDirection: 'ASC'}
	): Observable<OfficesTableItem[]> {
		let parameters = new HttpParams();
		parameters = parameters.append('pageNumber', `${params.pageNumber}`);
		parameters = parameters.append('pageSize', `${params.pageSize}`);
		parameters = parameters.append('sortMethod', params.sortMethod);
		parameters = parameters.append('sortDirection', params.sortDirection);

		return this.get<OfficesTableItem[]>(`/offices/extended/paging`, {params: parameters}).pipe(
			map((data: any) => {
				return data.map((item: any) => {
					return {
						country: item.country.name,
						city: item.city.name,
						address: item.office.street,
						floors: item.floors,
						officeId: item.office.id
					};
				});
			})
		);
	}

	getOfficesCount(): Observable<number> {
		return this.get<number>(`/offices/count`);
	}

	deleteOffice(officeId: number): Observable<OfficesTableItem> {
		return this.delete(`/offices/${officeId}`);
	}
}
