import {BrowserModule} from '@angular/platform-browser';
import {NgModule} from '@angular/core';

import {AppRoutingModule} from './app-routing.module';
import {AppComponent} from './app.component';
import {BrowserAnimationsModule} from '@angular/platform-browser/animations';
import {HeaderModule} from '@shared/header/header.module';
import {AuthenticationModule} from '@pages/authentication/authentication.module';
import {MainPageComponent} from '@pages/main-page/main-page.component';
import {SideNavModule} from '@pages/main-page/side-nav-home-page/side-nav-module';
import {HTTP_INTERCEPTORS, HttpClientModule} from '@angular/common/http';
import {ApiService} from './api.service';
import {DashboardComponent} from '@pages/booking-search/dashboard/dashboard.component';
import {MAT_DATE_LOCALE, MatNativeDateModule} from '@angular/material/core';
import {BookingSearchModule} from '@pages/booking-search/booking-search.module';
import {OfficesModule} from '@pages/offices/offices.module';

import {HttpRequestInterceptor} from '@shared/services/http-request-interceptor';
import {MatButtonModule} from '@angular/material/button';
import {LayoutModule} from '@angular/cdk/layout';
import {MatProgressSpinnerModule} from '@angular/material/progress-spinner';
import {HeaderService} from '@shared/header/header.service';
import {MatFormFieldModule} from '@angular/material/form-field';
import {MatSelectModule} from '@angular/material/select';
import {MapBuilderModule} from '@shared/map-builder/map-builder-module';
import {UserManagementModule} from '@pages/user-management/user-management.module';
import {StaffBookingsModule} from '@pages/staff-bookings/staff-bookings.module';
import {AddingStaffBookingsModule} from '@pages/adding-staff-bookings/adding-staff-bookings.module';
import {MapForBookingModule} from '@shared/map-for-booking/map-for-booking-module';
import {UserBookingsModule} from '@pages/user-bookings/user-bookings.module';
import {MatDialogModule} from '@angular/material/dialog';
// import { CancelConfirmationComponent } from './pages/main-page/side-nav-home-page/cancel-confirmation/cancel-confirmation.component';

@NgModule({
	declarations: [AppComponent, MainPageComponent, DashboardComponent /*, CancelConfirmationComponent*/],
	imports: [
		BrowserModule,
		AppRoutingModule,
		BrowserAnimationsModule,
		SideNavModule,
		HttpClientModule,
		MatButtonModule,
		HeaderModule,
		AuthenticationModule,
		SideNavModule,
		OfficesModule,
		MatNativeDateModule,
		BookingSearchModule,
		LayoutModule,
		MatProgressSpinnerModule,
		UserManagementModule,
		MatFormFieldModule,
		MatSelectModule,
		MapBuilderModule,
		StaffBookingsModule,
		AddingStaffBookingsModule,
		MapForBookingModule,
		MatDialogModule,
		MapForBookingModule,
		UserBookingsModule
	],
	providers: [
		{provide: HTTP_INTERCEPTORS, useClass: HttpRequestInterceptor, multi: true},
		ApiService,
		{provide: MAT_DATE_LOCALE, useValue: 'en-GB'},
		HeaderService
	],
	bootstrap: [AppComponent]
})
export class AppModule {}
