import {Component, OnInit, OnDestroy} from '@angular/core';
import {Observable, Subscription} from 'rxjs';
import {OfficesService} from '@pages/offices/offices.service';
import {take} from 'rxjs/internal/operators';
import {MatDialog} from '@angular/material/dialog';
import {OfficeCreationComponent} from '@pages/office-creation/office-creation.component';
import {GetOfficesParams, OfficesTableItem} from './entities';
import {WarningDialogComponent} from '@pages/offices/warning-dialog/warning-dialog.component';
import {DeleteParams} from '@pages/offices/entities';

@Component({
	selector: 'offices',
	templateUrl: './offices.component.html',
	styleUrls: ['./offices.component.scss']
})
export class OfficesComponent implements OnInit, OnDestroy {
	offices$;
	officesCount$;
	subscription: Subscription = new Subscription();

	constructor(private officesService: OfficesService, public dialog: MatDialog) {}

	ngOnInit(): void {
		const params = {pageNumber: 0, pageSize: 10, sortMethod: 'id', sortDirection: 'DESC'};
		this.offices$ = this.getOffices(params).pipe(take(1));
		this.officesCount$ = this.getOfficesCount().pipe(take(1));
	}

	ngOnDestroy() {
		this.subscription.unsubscribe();
	}

	getOffices(params?: GetOfficesParams): Observable<any[]> {
		return (this.offices$ = this.officesService.getOffices(params));
	}

	openOfficeCreationDlg() {
		this.subscription.add(
			this.dialog
				.open(OfficeCreationComponent)
				.afterClosed()
				.subscribe(() => {
					this.offices$ = this.getOffices().pipe(take(1));
					this.officesCount$ = this.getOfficesCount().pipe(take(1));
				})
		);
	}

	getOfficesCount() {
		return this.officesService.getOfficesCount();
	}

	updateTable(data: DeleteParams) {
		this.subscription.add(
			this.deleteOffice(data.officeId).subscribe(
				() => {
					this.offices$ = this.getOffices(data.params).pipe(take(1));
					this.officesCount$ = this.getOfficesCount().pipe(take(1));
				},
				(error) => {
					if (error.status === 409) {
						this.dialog.open(WarningDialogComponent, {
							data: {
								title: "Sorry, we can't delete this office",
								text: 'Some of its places are booked by employees.'
							}
						});
					}
				}
			)
		);
	}

	deleteOffice(officeId: number): Observable<OfficesTableItem> {
		return this.officesService.deleteOffice(officeId);
	}
}
