import {
	AfterViewInit,
	Component,
	EventEmitter,
	Input,
	OnChanges,
	OnInit,
	Output,
	SimpleChanges,
	ViewChild
} from '@angular/core';
import {GetUsersParams, Role, SetRolesParams, User} from '@pages/user-management/entities';
import {merge} from 'rxjs';
import {delay, map, startWith} from 'rxjs/internal/operators';
import {MatPaginator} from '@angular/material/paginator';
import {MatSort} from '@angular/material/sort';

@Component({
	selector: 'user-table',
	templateUrl: './user-table.component.html',
	styleUrls: ['./user-table.component.scss']
})
export class UserTableComponent implements OnInit, OnChanges, AfterViewInit {
	displayedColumns: string[] = ['name', 'email', 'hr', 'roles'];
	@ViewChild(MatPaginator) paginator: MatPaginator;
	@ViewChild(MatSort) sort: MatSort;
	@Input() users: User[];
	@Input() userCount: number;
	@Input() roles: Role[];
	@Output() getUsers = new EventEmitter<GetUsersParams>();
	@Output() getUserCount = new EventEmitter<void>();
	@Output() setUserRoles = new EventEmitter<SetRolesParams>();
	dataSource: User[] = [];
	mainRoles: string[] = ['HR', 'Admin', 'Office Manager'];
	loading = false;

	constructor() {}

	ngOnInit(): void {}

	setRole(user: User, role: string): void {
		const userIndex = this.dataSource.findIndex((userElement) => userElement.id === user.id);
		const hasRole = this.hasRole(this.dataSource[userIndex], role);

		const roleInd: number = this.roles.findIndex((roleElem) => roleElem.name.toLowerCase() === role.toLowerCase());

		if (hasRole) {
			this.dataSource[userIndex].roles = this.dataSource[userIndex].roles.filter(
				(userRole) => userRole.name !== role
			);
		} else {
			const newRole: Role = {id: this.roles[roleInd].id, name: role};
			this.dataSource[userIndex].roles.push(newRole);
		}
		this.setUserRoles.emit({roles: this.dataSource[userIndex].roles, userId: this.dataSource[userIndex].id});
	}

	hasRole(user, role): boolean {
		return !(user.roles.findIndex((userRole) => userRole.name.toLowerCase() === role.toLowerCase()) === -1);
	}

	ngOnChanges(changes: SimpleChanges) {
		if (changes.users && this.users) {
			this.dataSource = this.users;
		}
	}

	ngAfterViewInit() {
		this.sort.sortChange.subscribe(() => (this.paginator.pageIndex = 0));

		merge(this.sort.sortChange, this.paginator.page)
			.pipe(
				startWith({}),
				delay(0),
				map(() => {
					this.loading = true;
					this.getUsers.emit({
						pageNumber: this.paginator.pageIndex,
						pageSize: this.paginator.pageSize,
						sortMethod: this.sort.direction ? this.sort.active : 'id',
						sortDirection: this.sort.direction ? this.sort.direction.toUpperCase() : 'DESC',
						username: ''
					});
					this.getUserCount.emit();
					this.loading = false;
					return this.users;
				})
			)
			.subscribe((data) => {
				this.dataSource = data;
			});
	}
}
