import {Component, OnDestroy, OnInit} from '@angular/core';
import {GetUsersParams, Role, SetRolesParams, User} from '@pages/user-management/entities';
import {Observable, Subscription} from 'rxjs';
import {UsersService} from '@pages/user-management/users.service';
import {debounceTime, map, take} from 'rxjs/internal/operators';
import {FormControl, FormGroup} from '@angular/forms';

@Component({
	selector: 'user-management',
	templateUrl: './user-management.component.html',
	styleUrls: ['./user-management.component.scss']
})
export class UserManagementComponent implements OnInit, OnDestroy {
	users$;
	userCount$;
	roles$;
	subscription: Subscription = new Subscription();
	params: GetUsersParams = {pageNumber: 0, pageSize: 10, sortMethod: 'id', sortDirection: 'DESC', username: ''};
	formGroup: FormGroup;

	constructor(private usersService: UsersService) {}

	ngOnInit(): void {
		this.formGroup = new FormGroup({
			usernameControl: new FormControl()
		});

		this.users$ = this.getUsers(this.params).pipe(take(1));
		this.userCount$ = this.getUserCount().pipe(take(1));
		this.roles$ = this.getRoles().pipe(take(1));

		this.subscription.add(
			this.formGroup
				.get('usernameControl')
				.valueChanges.pipe(
					debounceTime(300),
					map((value) => {
						this.params.username = value;
						this.users$ = this.getUsers(this.params).pipe(take(1));
						this.userCount$ = this.getUserCount(value).pipe(take(1));
					})
				)
				.subscribe()
		);
	}

	ngOnDestroy() {
		this.subscription.unsubscribe();
	}

	getUsers(params?: GetUsersParams): Observable<User[]> {
		params.username = this.formGroup.get('usernameControl').value?.trim() || '';
		return (this.users$ = this.usersService.getUsers(params));
	}

	getUserCount(searchValue?: string): Observable<number> {
		searchValue = this.formGroup.get('usernameControl').value?.trim() || '';
		return (this.userCount$ = this.usersService.getUsersByNameCount(searchValue));
	}

	getRoles(): Observable<Role[]> {
		return (this.roles$ = this.usersService.getRoles());
	}

	setRoles(params: SetRolesParams) {
		this.subscription.add(this.usersService.setRoles(params).subscribe());
	}
}
