import {DataSource} from '@angular/cdk/collections';
import {MatPaginator} from '@angular/material/paginator';
import {MatSort} from '@angular/material/sort';
import {map} from 'rxjs/operators';
import {Observable, merge, BehaviorSubject} from 'rxjs';
import {StaffBookingsService} from '@pages/staff-bookings/staff-bookings.service';

export class StaffBTableDataSource extends DataSource<any> {
	private $data: BehaviorSubject<any>;
	set data(data: any[]) {
		this.$data.next(data);
	}

	get data() {
		return this.$data.value;
	}

	paginator: MatPaginator;
	sort: MatSort;

	constructor(private staffBookingsService: StaffBookingsService) {
		super();
		this.$data = new BehaviorSubject([]);
	}

	/**
	 * Connect this data source to the table. The table will only update when
	 * the returned stream emits new items.
	 * @returns A stream of the items to be rendered.
	 */
	connect(): Observable<any[]> {
		// Combine everything that affects the rendered data into one update
		// stream for the data-table to consume.
		const dataMutations = [this.$data, this.paginator.page, this.sort.sortChange];

		return merge(...dataMutations).pipe(
			map(() => {
				return this.getPagedData(this.getSortedData([...this.data]));
			})
		);
	}

	disconnect() {}

	private getPagedData(data: any[]) {
		const startIndex = this.paginator.pageIndex * this.paginator.pageSize;
		return data.splice(startIndex, this.paginator.pageSize);
	}

	private getSortedData(data: any[]) {
		if (!this.sort.active || this.sort.direction === '') {
			return data;
		}

		return data.sort((a, b) => {
			const isAsc = this.sort.direction === 'asc';
			switch (this.sort.active) {
				case 'employee':
					return compare(a.user.name, b.user.name, isAsc);
				case 'address':
					return compare(
						a.country.name + a.city.name + a.street,
						b.country.name + b.city.name + b.street,
						isAsc
					);
				case 'floor':
					return compare(+a.floor.number, +b.floor.number, isAsc);
				case 'place':
					return compare(a.place.name, b.place.name, isAsc);
				case 'date':
					return compare(a.date + a.startTime + a.endTime, b.date + b.startTime + b.endTime, isAsc);
				default:
					return 0;
			}
		});
	}
}

function compare(a: string | number, b: string | number, isAsc: boolean) {
	return (a < b ? -1 : 1) * (isAsc ? 1 : -1);
}
