import {BrowserModule} from '@angular/platform-browser';
import {NgModule} from '@angular/core';

import {HttpClientModule} from '@angular/common/http';

import {MatToolbarModule} from '@angular/material/toolbar';
import {RouterModule} from '@angular/router';
import {MatButtonModule} from '@angular/material/button';
import {MatIconModule} from '@angular/material/icon';
import {MapBuilderComponent} from '@shared/map-builder/map-builder.component';
import {MatInputModule} from '@angular/material/input';
import {MatSelectModule} from '@angular/material/select';
import {ReactiveFormsModule} from '@angular/forms';
import {MapBuilderService} from '@shared/map-builder/map-builder.service';
import {MatCardModule} from '@angular/material/card';
import {CustomDialogComponent} from '@shared/map-builder/custom-dialog/custom-dialog.component';
import {MatDialogModule} from '@angular/material/dialog';
import {LoadImageDialogComponent} from '@shared/map-builder/load-image-dialog/load-image-dialog.component';
import {MaterialFileInputModule} from 'ngx-material-file-input';
import {MatProgressSpinnerModule} from '@angular/material/progress-spinner';

@NgModule({
	declarations: [MapBuilderComponent, CustomDialogComponent, LoadImageDialogComponent],
	imports: [
		BrowserModule,
		HttpClientModule,
		MatToolbarModule,
		RouterModule,
		MatButtonModule,
		MatIconModule,
		MatInputModule,
		MatSelectModule,
		ReactiveFormsModule,
		MatCardModule,
		MatDialogModule,
		MaterialFileInputModule,
		MatProgressSpinnerModule
	],
	exports: [MapBuilderComponent],
	providers: [MapBuilderService]
})
export class MapBuilderModule {}
