import {ChangeDetectionStrategy, Component, Input, OnDestroy, OnInit} from '@angular/core';
import {AvailableFilters, PlaceOptions} from '@pages/booking-search/entities';
import {BookingSearchFiltersService} from '../booking-search-filters.service';
import {ActivatedRoute} from '@angular/router';
import {take} from 'rxjs/operators';
import {Subscription} from 'rxjs';
import {User} from '@entities/user';

@Component({
	selector: 'dashboard',
	templateUrl: './dashboard.component.html',
	styleUrls: ['./dashboard.component.scss'],
	changeDetection: ChangeDetectionStrategy.OnPush
})
export class DashboardComponent implements OnDestroy, OnInit {
	@Input() selectedFilters: AvailableFilters;
	cityId: number = +this.route.snapshot.paramMap.get('cityId');
	filters: string;
	hasData: boolean;
	places: PlaceOptions;
	showMapFlag = false;
	chosenPlaceId: number;
	private placesSubscription: Subscription;
	userId: number = +this.route.snapshot.paramMap.get('userId');
	bookingUserId: number;
	currentUser: User;
	private userSubscription: Subscription;
	private bookingUserSubscription: Subscription;
	bookingUser: User;

	constructor(public bookingSearchFiltersService: BookingSearchFiltersService, private route: ActivatedRoute) {}

	ngOnInit() {
		this.getUser();
		if (this.userId) {
			this.getBookingUser();
		} else {
			this.bookingUser = null;
		}
	}
	chosePlace(event: number) {
		this.chosenPlaceId = event;
	}

	getBookingUser(): void {
		this.bookingUserSubscription = this.bookingSearchFiltersService
			.getBookingUser(this.userId)
			.pipe(take(1))
			.subscribe((bookingUser) => (this.bookingUser = bookingUser));
	}

	getUser(): void {
		this.userSubscription = this.bookingSearchFiltersService
			.getCurrentUser()
			.pipe(take(1))
			.subscribe((currentUser) => (this.currentUser = currentUser));
	}

	filtersChange(event) {
		this.places = {
			floorId: null,
			places: []
		};
		this.filters = event;
		this.placesSubscription = this.bookingSearchFiltersService
			.getBookingPlacesIds(this.filters)
			.pipe(take(1))
			.subscribe((places) => {
				places.forEach((place) =>
					this.places.places.push({placeId: place.placeId, tags: place.tags.map((tag) => tag.name)})
				);
				this.places.floorId = event.floorId;
				console.log(event.floorId);
			});
	}
	ngOnDestroy() {
		if (this.placesSubscription) {
			this.placesSubscription.unsubscribe();
		}
		if (this.userSubscription) {
			this.userSubscription.unsubscribe();
		}
		if (this.bookingUserSubscription) {
			this.bookingUserSubscription.unsubscribe();
		}
	}
}
