import {Injectable} from '@angular/core';
import {ApiService} from '../../../api.service';
import {Floor} from '@pages/offices/entities';
import {Observable} from 'rxjs';

@Injectable()
export class FloorsService extends ApiService {
	addFloor(floor): Observable<Floor> {
		return this.post<Floor>(`/floors`, floor);
	}

	deleteFloor(floorId: number) {
		return this.delete(`/floors/${floorId}`);
	}
}
