import {Injectable} from '@angular/core';
import {Observable} from 'rxjs';
import {OrderModel} from '@pages/main-page/side-nav-home-page/entity-orders/order-model';
import {ApiService} from '../../../../api.service';

@Injectable()
export class OrderService extends ApiService {
	getOrders(): Observable<OrderModel[]> {
		return this.get<OrderModel[]>('/users/current/reservations');
	}
	getNearestOrders(): Observable<OrderModel> {
		return this.get<OrderModel>('/users/current/reservations/nearest');
	}

	deleteReservation(reservationId: number) {
		return this.delete<number>(`/reservations/${reservationId}`);
	}
}
