import {ChangeDetectionStrategy, Component, EventEmitter, Input, OnInit, Output, ViewChild} from '@angular/core';
import {FormControl, FormGroup, Validators} from '@angular/forms';
import {Router} from '@angular/router';
import {MatDialogRef} from '@angular/material/dialog';
import {FloorsDialogComponent} from '@pages/offices/floors-dialog/floors-dialog.component';
import {MatStep} from '@angular/material/stepper';
import {STEPPER_GLOBAL_OPTIONS} from '@angular/cdk/stepper';

@Component({
	selector: 'booking-form',
	templateUrl: './booking-form.component.html',
	styleUrls: ['./booking-form.component.scss'],
	changeDetection: ChangeDetectionStrategy.OnPush,
	providers: [
		{
			provide: STEPPER_GLOBAL_OPTIONS,
			useValue: {showError: true}
		}
	]
})
export class BookingFormComponent implements OnInit {
	countryId: number;
	@Input() employeesToChoose;
	@Input() countriesToChoose;
	@Output() setCountry = new EventEmitter<any>();
	@Input() citiesToChoose;
	@ViewChild('cityStep')
	cityStep: MatStep;

	constructor(private router: Router, private dialogRef: MatDialogRef<FloorsDialogComponent>) {}

	ngOnInit() {
		if (!this.countriesToChoose) {
			this.countriesToChoose = [];
		}
		if (!this.employeesToChoose) {
			this.employeesToChoose = [];
		}
		if (!this.citiesToChoose) {
			this.citiesToChoose = [];
		}
	}

	bookForm = new FormGroup({
		employee: new FormControl('', Validators.required),
		country: new FormControl('', Validators.required),
		city: new FormControl('', Validators.required)
	});

	countryChange() {
		if (this.bookForm.get('country').value !== this.countryId) {
			if (this.bookForm.get('country').value) {
				this.countryId = this.bookForm.get('country').value;
				this.setCountry.emit(this.countryId);
			}
			this.bookForm.get('city').setErrors({incorrect: true});
			this.cityStep.completed = false;
		}
	}

	submit(cityId: number, userId: number): void {
		this.router.navigateByUrl(`/booking-search/${cityId}/${userId}`);
		this.dialogRef.close();
	}

	selectionChange(event) {
		if (event.selectedIndex === 1) {
			if (this.bookForm.get('country').value !== this.countryId) {
				if (this.bookForm.get('country').value) {
					this.countryId = this.bookForm.get('country').value;
					this.setCountry.emit(this.countryId);
				}
			}
		}
	}
}
