import {ChangeDetectionStrategy, ChangeDetectorRef, Component, OnInit} from '@angular/core';
import {fabric} from 'fabric';
import {Rect} from 'fabric/fabric-impl';
import {FormControl} from '@angular/forms';
import {Observable} from 'rxjs';
import {MapBuilderService} from '@shared/map-builder/map-builder.service';
import {TagsModel} from '@shared/map-builder/tags-model';
import {PlaceMoreModel} from '@shared/map-builder/place-more-model';
import {ActivatedRoute} from '@angular/router';
import {HttpHeaders} from '@angular/common/http';
import {MatDialog} from '@angular/material/dialog';
import {CustomDialogComponent} from '@shared/map-builder/custom-dialog/custom-dialog.component';
import {LoadImageDialogComponent} from '@shared/map-builder/load-image-dialog/load-image-dialog.component';

@Component({
	selector: 'map-builder',
	templateUrl: './map-builder.component.html',
	styleUrls: ['./map-builder.component.scss'],
	changeDetection: ChangeDetectionStrategy.OnPush
})
export class MapBuilderComponent implements OnInit {
	canvas: any;
	wall: Rect;
	window: Rect;
	ID = 0;
	zoom: number;
	floorId: number = +this.route.snapshot.paramMap.get('floorId');
	svgMap: string;
	triggerOfEdit = false;
	svgMapNew$: Observable<string>;
	tagController = new FormControl();
	capacityController = new FormControl();
	nameController = new FormControl();
	tags$: Observable<TagsModel[]>;
	returnedPlaceInfo$: Observable<PlaceMoreModel[]>;
	placeInfoOnRedact$: Observable<PlaceMoreModel[]>;
	tags: TagsModel[] = [];
	placesInfo: PlaceMoreModel[] = [];
	loading = false;

	constructor(
		private serviceForOrders: MapBuilderService,
		private route: ActivatedRoute,
		private cd: ChangeDetectorRef,
		public dialog: MatDialog
	) {}

	ngOnInit(): void {
		this.getTags();
		this.tags$.subscribe((event) => event.forEach((elem) => this.tags.push(elem)));
		this.canvas = new fabric.Canvas('canvas', {allowTouchScrolling: true});
		this.canvas.preserveObjectStacking = true;
		this.canvas.on('mouse:wheel', (opt) => {
			const delta = opt.e.deltaY;
			this.zoom = this.canvas.getZoom();
			this.zoom *= 0.999 ** delta;
			if (this.zoom > 1) {
				this.zoom = 1;
			}
			if (this.zoom < 0.6) {
				this.zoom = 0.6;
			}
			console.log(this.zoom);
			this.canvas.zoomToPoint({x: opt.e.offsetX, y: opt.e.offsetY}, this.zoom);
			opt.e.preventDefault();
			opt.e.stopPropagation();
		});

		this.canvas.on('mouse:move', (opt) => {
			console.log(opt.e.offsetX + ' , ' + opt.e.offsetY);
		});
		this.loadFromSVG();
		this.getPlaceInfoOnRedact();
		this.placeInfoOnRedact$.subscribe((places) => {
			places.forEach((place) => {
				this.placesInfo.push(place);
			});
		});
		document.addEventListener('keydown', (e) => {
			console.log(e.code);
			if (e.code === 'Backspace' && this.triggerOfEdit !== true) {
				if (this.canvas.getActiveObject() && this.canvas.getActiveObject().id !== 'map') {
					this.placesInfo.splice(
						this.placesInfo.findIndex((current) => current.id === this.canvas.getActiveObject().id),
						1
					);
					console.log(this.placesInfo);
					this.canvas.remove(this.canvas.getActiveObject());
				} else {
					if (this.canvas.getActiveObject()) {
						this.canvas.remove(this.canvas.getActiveObject());
					}
				}
			}
			if (e.code === 'KeyR' && this.triggerOfEdit !== true) {
				if (this.canvas.getActiveObject()) {
					this.canvas.getActiveObject().rotate(this.canvas.getActiveObject().get('angle') + 90);
					console.log(this.canvas.getActiveObject());
				}
			}
			if (e.code === 'KeyF' && this.triggerOfEdit !== true) {
				if (this.canvas.getActiveObject()) {
					this.canvas.getActiveObject().flipX = !this.canvas.getActiveObject().flipX;
				}
			}
			if (e.code === 'KeyE' && this.triggerOfEdit !== true) {
				if (this.canvas.getActiveObject() && this.canvas.getActiveObject().id !== undefined) {
					this.triggerOfEdit = true;
					this.cd.detectChanges();
					document.getElementById('selectId').style.display = 'block';
				}
			}
			if (e.code === 'KeyG' && this.triggerOfEdit !== true) {
				if (this.canvas.getActiveObject() && this.canvas.getActiveObject().id !== undefined) {
					this.canvas.getActiveObject().filters.push(new fabric.Image.filters.Grayscale());
					this.canvas.getActiveObject().applyFilters();
					console.log(this.canvas.getActiveObject());
				}
			}
			if (e.code === 'KeyL' && this.triggerOfEdit !== true) {
				this.canvas.getObjects().forEach((obj) => {
					if (obj.id === 'map') {
						obj.selectable = !obj.selectable;
					}
				});
			}
			if (e.code === 'KeyC' && this.triggerOfEdit !== true) {
				if (this.canvas.getActiveObject() && this.canvas.getActiveObject().id !== undefined) {
					const clone = fabric.util.object.clone(this.canvas.getActiveObject());
					clone.left = clone.left + 10;
					clone.top = clone.top + 10;
					if (clone.id !== 'pot' && clone.id !== 'wall' && clone.id !== 'map' && clone.id !== 'window') {
						this.canvas.add(clone).setActiveObject(clone);
						this.canvas.getActiveObject().id = -(this.ID + 1);
						this.placesInfo.push({
							capacity: 1,
							floorId: this.floorId,
							id: -(this.ID + 1),
							name: 'place' + -(this.ID + 1),
							roles: [{id: 6, name: 'All'}],
							tags: []
						});
						this.ID += 1;
					}
					this.canvas.getActiveObject().applyFilters();
					console.log(this.canvas.getActiveObject());
				}
			}
			if (e.code === 'ArrowUp' && this.triggerOfEdit !== true) {
				if (this.canvas.getActiveObject() && this.canvas.getActiveObject().id !== undefined) {
					this.canvas.getActiveObject().top -= 1;
					console.log(this.canvas.getActiveObject());
				}
			}
			if (e.code === 'ArrowDown' && this.triggerOfEdit !== true) {
				if (this.canvas.getActiveObject() && this.canvas.getActiveObject().id !== undefined) {
					this.canvas.getActiveObject().top += 1;
					console.log(this.canvas.getActiveObject());
				}
			}
			if (e.code === 'ArrowLeft' && this.triggerOfEdit !== true) {
				if (this.canvas.getActiveObject() && this.canvas.getActiveObject().id !== undefined) {
					this.canvas.getActiveObject().left -= 1;
					console.log(this.canvas.getActiveObject());
				}
			}
			if (e.code === 'ArrowRight' && this.triggerOfEdit !== true) {
				if (this.canvas.getActiveObject() && this.canvas.getActiveObject().id !== undefined) {
					this.canvas.getActiveObject().left += 1;
					console.log(this.canvas.getActiveObject());
				}
			}
			this.canvas.renderAll();
		});
	}

	getTags(): Observable<TagsModel[]> {
		return (this.tags$ = this.serviceForOrders.getTags());
	}

	getRefactoredPlaceInfo(): Observable<PlaceMoreModel[]> {
		return (this.returnedPlaceInfo$ = this.serviceForOrders.resetPlacesId(this.floorId, this.placesInfo));
	}

	getPlaceInfoOnRedact(): Observable<PlaceMoreModel[]> {
		return (this.placeInfoOnRedact$ = this.serviceForOrders.getExistedPlaces(this.floorId));
	}

	postSVG(svgMap: string): void {
		this.serviceForOrders.postMap(this.floorId, svgMap);
	}

	addMeetingRoom() {
		fabric.Image.fromURL('../assets/static_picture/mr.png', (img) => {
			img.setControlsVisibility({
				mt: false,
				mb: false,
				ml: false,
				mr: false
			});
			img.left = 80;
			img.top = 80;
			this.canvas.add(img).setActiveObject(img);
			this.canvas.getActiveObject().id = -(this.ID + 1);
			this.placesInfo.push({
				capacity: 1,
				floorId: this.floorId,
				id: -(this.ID + 1),
				name: 'place' + -(this.ID + 1),
				roles: [{id: 6, name: 'All'}],
				tags: []
			});
			this.ID += 1;
		});
	}

	addSpecialPlace() {
		fabric.Image.fromURL('../assets/static_picture/SP.png', (img) => {
			img.setControlsVisibility({
				mt: false,
				mb: false,
				ml: false,
				mr: false
			});
			img.left = 80;
			img.top = 80;
			this.canvas.add(img).setActiveObject(img);
			this.canvas.getActiveObject().id = -(this.ID + 1);
			this.placesInfo.push({
				capacity: 1,
				floorId: this.floorId,
				id: -(this.ID + 1),
				name: 'place' + -(this.ID + 1),
				roles: [{id: 6, name: 'All'}],
				tags: []
			});
			this.ID += 1;
		});
	}

	addWorkPlace() {
		fabric.Image.fromURL('../assets/static_picture/wp.png', (img) => {
			img.setControlsVisibility({
				mt: false,
				mb: false,
				ml: false,
				mr: false
			});
			img.left = 80;
			img.top = 80;
			this.canvas.add(img).setActiveObject(img);
			this.canvas.getActiveObject().id = -(this.ID + 1);
			this.placesInfo.push({
				capacity: 1,
				floorId: this.floorId,
				id: -(this.ID + 1),
				name: 'place' + -(this.ID + 1),
				roles: [{id: 6, name: 'All'}],
				tags: []
			});
			this.ID += 1;
		});
	}

	addMap() {
		const dialogRef = this.dialog.open(LoadImageDialogComponent, {
			data: {floorId: this.floorId, link: `http://hotchairs.herokuapp.com/api/floors/plan/default`}
		});

		dialogRef.afterClosed().subscribe((result) => {
			fabric.Image.fromURL(result, (img) => {
				img.setControlsVisibility({
					mt: false,
					mb: false,
					ml: false,
					mr: false
				});
				img.left = 80;
				img.top = 80;
				img.crossOrigin = 'Anonymous';
				this.canvas.add(img).setActiveObject(img);
				this.canvas.getActiveObject().id = 'map';
			});
		});
	}

	addWindow() {
		this.window = new fabric.Rect({
			left: 80,
			top: 80,
			width: 50,
			height: 3,
			fill: 'blue'
		});
		this.window.setControlsVisibility({
			mt: false,
			mb: false,
			bl: false,
			tl: false,
			br: false,
			tr: false
		});
		this.window.lockScalingY = true;
		this.canvas.add(this.window).setActiveObject(this.window);
		this.canvas.getActiveObject().id = 'window';
	}

	addWall() {
		this.wall = new fabric.Rect({
			left: 80,
			top: 80,
			width: 100,
			height: 2,
			fill: 'black'
		});
		this.wall.setControlsVisibility({
			mt: false,
			mb: false,
			bl: false,
			tl: false,
			br: false,
			tr: false
		});
		this.wall.lockScalingY = true;
		this.canvas.add(this.wall).setActiveObject(this.wall);
		this.canvas.getActiveObject().id = 'wall';
	}

	addPot() {
		fabric.Image.fromURL('../assets/static_picture/pot.png', (img) => {
			img.set({
				scaleX: 20 / img.width,
				scaleY: 20 / img.height
			});
			img.setControlsVisibility({
				mt: false,
				mb: false,
				bl: false,
				tl: false,
				br: false,
				tr: false,
				mr: false,
				ml: false
			});
			img.lockScalingX = true;
			img.lockScalingY = true;
			img.left = 80;
			img.top = 80;
			this.canvas.add(img).setActiveObject(img);
			this.canvas.getActiveObject().id = 'pot';
		});
	}

	getConsultationPointSOAPWs() {
		return (this.svgMapNew$ = this.serviceForOrders.getMap(this.floorId, {
			headers: new HttpHeaders({
				'Content-Type': 'application/xml'
			}),
			responseType: 'text'
		}));
		// return (this.SVGSHE4KANEW$ = this.http.get(url, {
		// 	headers: new HttpHeaders({
		// 		'Content-Type': 'application/xml'
		// 	}),
		// 	responseType: 'text'
		// }));
	}

	convert() {
		this.loading = true;
		this.cd.detectChanges();
		this.getRefactoredPlaceInfo();
		this.zoom = this.canvas.getZoom();
		this.canvas.setZoom(1);
		this.returnedPlaceInfo$.subscribe((event) => {
			for (let i = 0; i < event.length; i++) {
				this.canvas.getObjects().forEach((object) => {
					if (object.id === this.placesInfo[i].id) {
						object.id = event[i].id;
						console.log(event[i]);
					}
				});
				this.placesInfo[i].id = event[i].id;
			}
			console.log(event);
			console.log(this.placesInfo);
			this.canvas.renderAll();
			this.svgMap = this.canvas.toSVG();
			console.log(this.svgMap);
			this.postSVG(this.svgMap.toString());
			this.canvas.setZoom(this.zoom);
			this.loading = false;
			this.cd.detectChanges();
		});
	}

	loadFromSVG() {
		const cnv = this.canvas;
		cnv.clear();
		cnv.setZoom(0.6);
		this.getConsultationPointSOAPWs();
		this.svgMapNew$.subscribe((elem) => {
			fabric.loadSVGFromString(elem, function(objects) {
				const offsetLeft = objects[0].left;
				const offsetTop = objects[0].top;
				objects.forEach((obj) => {
					obj.left = obj.left - offsetLeft + document.getElementById('canvasBlock').offsetWidth / 2;
					obj.top = obj.top - offsetTop + document.getElementById('canvasBlock').offsetHeight / 8;
					obj.setControlsVisibility({
						mt: false,
						mb: false,
						ml: false,
						mr: false
					});
					cnv.add(obj).renderAll();
				});
			});
		});
	}

	confirmAdditions() {
		console.log(this.capacityController.value);
		console.log(this.tagController.value);
		const tagsForConfirm: TagsModel[] = [];
		if (this.tagController.value !== null) {
			this.tagController.value.forEach((elem) =>
				tagsForConfirm.push(this.tags.find((current) => current.id === elem))
			);
		}
		console.log(tagsForConfirm);
		this.placesInfo.splice(
			this.placesInfo.findIndex((current) => current.id === this.canvas.getActiveObject().id),
			1,
			{
				capacity: this.capacityController.value !== null ? this.capacityController.value : 1,
				floorId: this.floorId,
				id: this.canvas.getActiveObject().id,
				name:
					this.nameController.value !== null
						? this.nameController.value.toUpperCase()
						: 'place-' + this.canvas.getActiveObject().id,
				roles: [{id: 6, name: 'All'}],
				tags: tagsForConfirm
			}
		);
		console.log(this.placesInfo);
		document.getElementById('selectId').style.display = 'none';
		this.tagController.reset();
		this.capacityController.reset();
		this.nameController.reset();
		this.triggerOfEdit = false;
		this.cd.detectChanges();
	}

	clearCanvas() {
		const cnv = this.canvas;
		cnv.clear();
		cnv.renderAll();
	}

	infoGet() {
		this.dialog.open(CustomDialogComponent);
	}
}
