import {Injectable} from '@angular/core';
import {HttpClient, HttpHeaders} from '@angular/common/http';
import {Observable, throwError} from 'rxjs';
import {catchError} from 'rxjs/operators';
import {Router} from '@angular/router';
import {UserService} from '@shared/services/user.service';

@Injectable({
	providedIn: 'root'
})
export class ApiService {
	private httpOptions = {
		headers: new HttpHeaders({'Content-Type': 'application/json'})
	};
	private apiUrl = 'https://hotchairs.herokuapp.com/api';

	constructor(private http: HttpClient, private router: Router, private userService: UserService) {}

	get<T>(url: string, httpOptions?): Observable<T> {
		return this.http.get<T>(this.apiUrl + url, httpOptions).pipe(this.errorCatching());
	}

	post<T>(url: string, data: any): Observable<T> {
		return this.http.post<T>(this.apiUrl + url, data, this.httpOptions).pipe(this.errorCatching());
	}

	put<T>(url: string, data: any): Observable<T> {
		return this.http.put<T>(this.apiUrl + url, data, this.httpOptions).pipe(this.errorCatching());
	}

	delete<T>(url: string): Observable<T> {
		return this.http.delete<T>(this.apiUrl + url, this.httpOptions).pipe(this.errorCatching());
	}

	private errorCatching(): any {
		return catchError((err) => {
			if (err.status === 401) {
				this.userService.user$.next(null);
				this.router.navigateByUrl('/login');
			}
			return throwError(err);
		});
	}
}
