import {Injectable} from '@angular/core';
import {ApiService} from '../../api.service';
import {Observable} from 'rxjs';
import {PlaceMoreModel} from '@shared/map-builder/place-more-model';

@Injectable()
export class MapForBookingService extends ApiService {
	getMap(floorId: number, params): Observable<string> {
		return this.get<string>(`/floors/map/${floorId}`, params);
	}
	getPlaceInfo(placeId: number): Observable<PlaceMoreModel> {
		return this.get<PlaceMoreModel>(`/places/${placeId}`);
	}
}
