import {NgModule} from '@angular/core';
import {BrowserAnimationsModule} from '@angular/platform-browser/animations';
import {CountryCardComponent} from '@shared/country-card/country-card.component';
import {MatCardModule} from '@angular/material/card';
import {MatChipsModule} from '@angular/material/chips';
import {CardService} from '@shared/country-card/entity/card.service';
import {CommonModule} from '@angular/common';
import {RouterModule} from '@angular/router';

@NgModule({
	declarations: [CountryCardComponent],
	imports: [MatCardModule, MatChipsModule, CommonModule, BrowserAnimationsModule, RouterModule],
	exports: [CountryCardComponent],
	providers: [CardService]
})
export class CardModule {}
